@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Orbitron:wght@400;500;600;700&display=swap);
*,*:before,*:after{box-sizing:border-box;margin:0;padding:0}*::selection{background-color:#ff4500}main,nav,footer,aside{margin:auto 2.5rem}html,body{background:#1e1d1d;color:#fff;scroll-behavior:smooth}body{display:-webkit-flex;display:flex;font-size:20px;font-family:'Orbitron', sans-serif}#root{position:relative}body,html{background-image:linear-gradient(to left, rgba(0,0,0,0.734) 100%, transparent 0%),url("/static/media/—Pngtree—board lines_1038031.71aa84a5.png");background-repeat:repeat;background-blend-mode:multiply}nav{box-shadow:0px 5px 15px rgba(1,1,1,0.15);position:absolute;background-color:#1e1d1d;left:0px;top:105px;width:-webkit-fill-available;height:30rem;margin-right:1rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;border-radius:15px;visibility:hidden}nav ul{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}li{list-style:none;padding-top:1rem}li a{text-decoration:none;color:#fff;padding-top:1rem}li a:hover,li:hover{color:#FE815B}.hamburger{margin:auto 1.5rem;margin-top:2rem}.flex{display:-webkit-flex;display:flex}.d-row{-webkit-flex-direction:row;flex-direction:row}.d-column{-webkit-flex-direction:column;flex-direction:column}.justify-content-center{-webkit-justify-content:center;justify-content:center}.cta-btn{background-color:#ff4500;color:#1e1d1d;padding:1.2rem 3.5rem;box-shadow:0px 6px 30px rgba(255,68,0,0.13);border-radius:5px;border:none;font-weight:600;font-size:1em;cursor:pointer;translate:transformY(2px);font-family:'Orbitron', sans-serif}.profile-img{width:10rem;height:10rem;border-radius:50%;box-shadow:0px 8px 24px rgba(0,0,0,0.15);object-fit:fill}.info-para{text-align:center;line-height:1.6}h1{text-transform:uppercase;font-size:3rem;padding-bottom:1rem}.info{-webkit-align-items:center;align-items:center}.info,.cta,.profile{margin:1rem auto}.underline{background:linear-gradient(to top, orangered 30%, transparent 70%)}

